.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 20px;
    background-color: #CBD0D8;
  }
  
  .header {
    text-align: center;
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    width: 100%;
    max-width: 600px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
  }
  
  .crack-container {
    background-color: #626262;
    padding: 20px;
    border-radius: 10px;
    width: 100%;
    max-width: 600px;
    text-align: center;
  }
  
  .crack-container h3 {
    color: white;
    margin-bottom: 20px;
  }
  
  .crack-card {
    background-color: white;
    padding: 10px;
    margin: 10px auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .crack-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 10px;
  }
  
  .crack-info p {
    margin: 5px 0;
    word-break: break-word;
  }
  
  .crack-image {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  /* 미디어 쿼리를 사용하여 반응형 디자인을 적용 */
  @media (max-width: 768px) {
    .header, .crack-container {
      padding: 10px;
    }
  
    .crack-card {
      padding: 10px;
      margin: 5px auto;
    }
  
    .crack-info p {
      font-size: 0.9em;
    }
  }
  
  @media (max-width: 480px) {
    .header, .crack-container {
      padding: 5px;
    }
  
    .crack-card {
      padding: 5px;
      margin: 5px auto;
    }
  
    .crack-info {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .crack-info p {
      font-size: 0.8em;
      margin: 2px 0;
    }
  }
  